table {
  max-width: 100%;
  padding: 0em;
  margin: 0;
	border: 1px solid #000000;
	border-collapse: collapse;
}
tr {
 padding: .2em;
}
td {
	border: 1px solid #000000;
	padding: 5px;
  font-size: 12pt;
  vertical-align: top;
  background: #ebf2f5;
}

th {
	font-weight: bold;
	padding: 5px;
  background: #d3dcff;
	border: 2px solid #000000;
  text-align: center;
  vertical-align: top;
  font-size: 14pt;
}
.Table{
  max-width: 1200px;
  margin: 2em auto;
}
.lilSpan{
  font-size: 10px;
}
.tdCenter{
 text-align: center;
}
.nPair{
  background-color: #efefef;
	border: 2px solid #000000;
  width: 10px;
}
.blacks td {
  padding: 0px;
  height: 1px;
  background: black;
}