.App {
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
}

.mainRow{
  width: 1400px;
  margin: 0 auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

header{
	background: #283975;
	height: 133px;
	display: flex;
	align-content: center;
	align-items: center;
	font-size: 3rem;
	font-weight:bold;
	padding: 20px 0px 0px 14%;
	color:#F9FFF4;
}
h1{
  text-align: left;
  margin: .5em 0;
  text-decoration: underline;
}
.failed{
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 1em 0;
  font-weight: bold;
  font-size: 20px;
  color: brown;
}
.failed *{
  margin: 0;
}
form{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}
.changeField{
  display: flex;
}
.changeField *{
  margin: 0 .2em;
}
.arrows{
  color: darkred;
}
.change{
	font-size: 18pt;
	height: fit-content;
	padding: 5px;
  font-weight: bold;
  width: 150px;
  background: #d3dcff;
  border: 1px solid #8088a4;
  cursor: pointer;
  margin: 1em;
}
.loader{
  z-index: 1;
  position: absolute;
  width: 100%;
  height: 85vh;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}